import React, { useEffect } from "react";

import MainCategoriesCards2 from "../../templates/maincategories/maincategoriescards2/MainCategoriesCards2";

import Navbar from "../../components/genral_components/Navbar/Navbar";

import MainCategoriesBanner from "../../templates/maincategories/maincategoriesbanner/MainCategoriesBanner";

import VideoMarketingDescription from "../../components/video_marketing_services_page_components/video-marketing-services-description/VideoMarketingDescription";

import Carousel from "../../components/home_page_components/carousel/Carousel";

import Pricing from "../../components/services_page_components/pricing/Pricing";

import Skills from "../../components/home_page_components/ourskills/Skills";

import OurWork from "../../components/home_page_components/ourwork/Ourwork";

import Footer from "../../components/genral_components/Footer/Footer";

import backgroundImage from "./VideoMarketingBanner.jpg";

import { useLocation } from "react-router-dom";

import pic1 from "./pic1.png"
import pic2 from "./pic2.png"

export default function VideoMarketingServicesPage() {
  const location = useLocation();

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className="video-marketing-services-page-main-container">
      <Navbar />

      <MainCategoriesBanner
        heading="Video Marketing"
        paragraph="Expand Your Business Horizons With Our Video Marketing Services!"
        button1="Let's Chat"
        button2="Our work"
        bannerBackgroundImage={backgroundImage}
      />

      <VideoMarketingDescription paragraph="Our video marketing agency is proud of the excellent service we provide and the smooth process we guarantee our clients. Building lasting bonds with our customers is a top priority, and we're committed to employing the persuasive potential of video to aid in your advertising efforts." />

      <MainCategoriesCards2
        cardOneHeading="Animated product promo Ads"
        cardOneParagraph="We bring your products to life and captivate your audience with stunning animated product promo ads, and marketing videos that drive sales and leave a lasting impression."
        cardOneLink="/services/videomarketingservices/animatedproductpromoads"
        cardTwoHeading="Animated Design"
        cardTwoParagraph="At interac8, we specialize in delivering mesmerizing animated designs that elevate your brand's visual presence. With our expert team of talented animators and designers, we transform your ideas into captivating animations that grab attention and engage your target audience."
        cardTwoLink="/services/videomarketingservices/animateddesign"
        cardThreeHeading="Social Media Reels"
        cardThreeParagraph="Our talented team of content creators and videographers knows how to craft the perfect social media reel that resonates with your target audience. From attention-grabbing visuals to engaging storytelling, we create dynamic and immersive experiences that keep viewers hooked from the first second to the last."
        cardThreeLink="/services/videomarketingservices/socialmediareels"
        cardFourHeading="Explainer Animations"
        cardFourParagraph="Explainer animations are short, engaging videos that simplify complex ideas, products, or services into easily understandable and visually compelling narratives. They typically utilize a combination of animation, motion graphics, and storytelling techniques to break down information, clarify concepts, and communicate key messages effectively. From product launches to educational campaigns, our explainer animations are the perfect tool to inform, inspire, and leave a lasting impact."
        cardFourLink="/services/videomarketingservices/explaineranimations"
        pic1={pic1}
        pic2={pic2}
      />

      <Carousel
        nameOne="Lily Anderson"
        reviewOne="I wanted a unique website that represented the firm's standards and values and could successfully express the message. Interac8 expertly crafted it, giving my company a noticeable and identifiable look with its website design services!"
        nameTwo="Ryan Smith"
        reviewTwo="The video marketing services provided by Interac8 were excellent. Our expectations were exceeded by their original strategy and competent implementation. Highly recommended!"
        nameThree="Gavin Clark"
        reviewThree="Interac8 was fantastic to work with when it came to video marketing. Their films captivated our audience, which raised brand recognition and generated more sales. I'm really happy!"
      />

      <br />
      <br />
      <br />
      <br />

      <Pricing />

      <br />
      <br />
      <br />

      <Skills />

      <br />
      <br />

      <OurWork />

      <Footer />
    </div>
  );
}
