import React , {useState} from "react";

import "./MainCategoriesCards2.css";

import { FaCalendar } from "react-icons/fa";

import "intersection-observer";

import arrow_left from "./arrow-left.png";
import arrow_right from "./arrow-right-to-left.png";

import triangle1 from "./triangle1.png";
import triangle2 from "./triangle2.png";

import { Link } from "react-router-dom";

import pic1 from "./pic1.png";
import pic2 from "./pic2.png";

export default function MainCategoriesCards2(props) {

  const [hoverCard1, setHoverCard1] = useState(false);

  const handleMouseEnterOne = () => {
    setHoverCard1(true);
  };

  const handleMouseLeaveOne = () => {
    setHoverCard1(false);
  };

  const [hoverCard2, setHoverCard2] = useState(false);

  const handleMouseEnterTwo = () => {
    setHoverCard2(true);
  };

  const handleMouseLeaveTwo = () => {
    setHoverCard2(false);
  };

  const [hoverCard3, setHoverCard3] = useState(false);

  const handleMouseEnterThree = () => {
    setHoverCard3(true);
  };

  const handleMouseLeaveThree = () => {
    setHoverCard3(false);
  };

  const [hoverCard4, setHoverCard4] = useState(false);

  const handleMouseEnterFour = () => {
    setHoverCard4(true);
  };

  const handleMouseLeaveFour = () => {
    setHoverCard4(false);
  };

  return (
    <div className="video-marketing-srvices-page-main-container">
      <div className="video-marketing-srvices-page-main-container-row-1">
        <Link
          to={props.cardOneLink}
          className="video-marketing-srvices-page-main-container-row-1-card-1"
          onMouseEnter={handleMouseEnterOne}
          onMouseLeave={handleMouseLeaveOne}
        >
          <FaCalendar
            size={50}
            color={hoverCard1 ? "#fff" : "#612d91"}
            className="calendar"
          />

          <br />

          <h1>{props.cardOneHeading}</h1>

          <br />

          <p>
            {props.cardOneParagraph}
          </p>

          <img src={arrow_left} className="web-arrow-1" alt="an-arrow" />

          <img src={triangle1} className="web-triangle-1" alt="triangle 1" />
        </Link>

        <Link
          to={props.cardTwoLink}
          className="video-marketing-srvices-page-main-container-row-1-card-2"
          onMouseEnter={handleMouseEnterTwo}
          onMouseLeave={handleMouseLeaveTwo}
        >
          <FaCalendar
            size={50}
            color={hoverCard2 ? "#fff" : "#612d91"}
            className="calendar"
          />

          <br />

          <h1>{props.cardTwoHeading}</h1>

          <br />

          <p>
            {props.cardTwoParagraph}
          </p>

          <img src={arrow_right} className="web-arrow-2" alt="an-arrow" />

          <img src={triangle2} className="web-triangle-2" alt="triangle 2" />
        </Link>

        <Link
          to={props.cardThreeLink}
          className="video-marketing-srvices-page-main-container-row-1-card-3"
          onMouseEnter={handleMouseEnterThree}
          onMouseLeave={handleMouseLeaveThree}
        >
          <FaCalendar
            size={50}
            color={hoverCard3 ? "#fff" : "#612d91"}
            className="calendar"
          />

          <br />

          <h1>{props.cardThreeHeading}</h1>

          <br />

          <p>
            {props.cardThreeParagraph}
          </p>

          <img src={arrow_left} className="web-arrow-1" alt="an-arrow" />

          <img src={triangle1} className="web-triangle-1" alt="triangle 1" />
        </Link>

        <Link
          to={props.cardFourLink}
          className="video-marketing-srvices-page-main-container-row-1-card-4"
          onMouseEnter={handleMouseEnterFour}
          onMouseLeave={handleMouseLeaveFour}
        >
          <FaCalendar
            size={50}
            color={hoverCard4 ? "#fff" : "#612d91"}
            className="calendar"
          />

          <br />

          <h1>{props.cardFourHeading}</h1>

          <br />

          <p>
            {props.cardFourParagraph}
          </p>

          <img src={triangle2} className="web-triangle-2" alt="triangle 2" />
        </Link>
      </div>

      <div className="video-marketing-srvices-page-main-container-row-2">
        <div className="video-marketing-srvices-page-main-container-row-2-pic1">
          <img src={props.pic1} alt="pic 20 here" style={{ width: "80%" }} />
        </div>

        <div className="video-marketing-srvices-page-main-container-row-2-pic2">
          <img src={props.pic2} alt="pic 20 here" style={{ width: "80%" }} />
        </div>
      </div>
    </div>
  );
}
